body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.body {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.optionsBody {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}

.title {
  flex-basis: 25%;
  background-color: rgb(92, 130, 164);
  display: flex;
}

.tiles {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  background-color: rgb(92, 130, 164);
}
.tile {
  background-color: #eee;
  box-sizing: border-box;
  margin: 2px;
  justify-content: center;
  display: flex;
  align-items: center;
}
.titleTiles {
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  margin-left: 2px;
}
.titleTile {
  background-color: #eee;
  box-sizing: border-box;
  margin: 0.5px;
  justify-content: center;
  display: flex;
  align-items: center;
}

.gamebody {
  display: flex;
  flex-direction: column;
}

.floatingBody {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.hintBody {
  width: fit-content;
  position: relative;
  background-color: #181a1b;
  display: flex;
  flex-wrap: wrap;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
}
.floatingClose {
  cursor: pointer;
  width: fit-content;
  font-size: 20px;
  position: absolute;
  top: 0px;
  right: 10px;
  color: red;
}

.STile {
  /* background-color: #eee; */
  box-sizing: border-box;
  margin: 0.5px;
  display: flex;
  flex-wrap: wrap;
}

.hintTile {
  background-color: #eee;
  box-sizing: border-box;
  margin: 0.5px;
  justify-content: center;
  display: flex;
  align-items: center;
}
